import * as React from 'react'
import { FC } from 'react'

import { Menu } from '@mui/material'

import { MenuItemComponent, MenuItemType } from './MenuItemComponent/MenuItemComponent'

type MenuComponentProps = {
  anchorEl: null | HTMLElement
  open: boolean
  handleClose: () => void
  items: MenuItemType[]
}

export const MenuComponent: FC<MenuComponentProps> = ({ handleClose, anchorEl, open, items }) => {
  const menuItems = items.map(item => (
    <MenuItemComponent title={item.title} image={item.image} key={item.id} onClick={item.onClick} />
  ))

  return (
    <Menu
      anchorEl={anchorEl}
      id="profile-menu"
      open={open}
      onClose={handleClose}
      onClick={handleClose}
      PaperProps={{
        elevation: 0,
        sx: {
          overflow: 'visible',
          filter: 'drop-shadow(0px 2px 8px rgba(0,0,0,0.32))',
          mt: 1.5,
          '& .MuiAvatar-root': {
            width: 32,
            height: 32,
            ml: -0.5,
            mr: 1,
          },
          '&:before': {
            content: '""',
            display: 'block',
            position: 'absolute',
            top: 0,
            right: 14,
            width: 10,
            height: 10,
            bgcolor: 'background.paper',
            transform: 'translateY(-50%) rotate(45deg)',
            zIndex: 0,
          },
        },
      }}
      transformOrigin={{ horizontal: 'right', vertical: 'top' }}
      anchorOrigin={{ horizontal: 'right', vertical: 'bottom' }}
    >
      {menuItems}
    </Menu>
  )
}
